@import "src/assets/scss/theme_color";
@import "src/assets/scss/variables";
@import "src/assets/scss/mixin";
@import "src/assets/scss/responsive";
/* :: 10.0 Single Product Details Area CSS */

.fb-comment-plugin-container {
  width: 100%;
  text-align: center;
}

.single_product_details_area {
  position: relative;
  z-index: 100;
  @include flex-wrap(wrap);
  .single_product_thumb,
  .single_product_desc {
    @include flex(0 0 50%);
    max-width: 50%;
    width: 50%;
    position: relative;
    z-index: 1;
    @media #{$breakpoint-xs} {
      @include flex(0 0 100%);
      max-width: 100%;
      width: 100%;
    }
  }
  .product_thumbnail_slides {
    position: relative;
    z-index: 1;
    .owl-prev,
    .owl-next {
      @include trans-duration(500ms);
      position: absolute;
      top: 50%;
      z-index: 20;
      left: 30px;
      width: 40px;
      height: 40px;
      margin-top: -20px;
      padding: 5px;
      opacity: 0.4;
      &:hover {
        opacity: 1;
      }
    }
    .owl-next {
      left: auto;
      right: 30px;
    }
  }
  .single_product_desc {
    position: relative;
    z-index: 1;
    padding: 50px 5%;
    @media #{$breakpoint-lg} {
      padding: 30px 4%;
    }
    @media #{$breakpoint-md} {
      padding: 30px 3%;
    }
    span {
      font-size: 14px;
      text-transform: uppercase;
      font-weight: 600;
      color: $text-color;
      margin-bottom: 10px;
      display: block;
    }
    a > h2 {
      font-size: 30px;
      display: block;
      margin-bottom: 10px;
      @media #{$breakpoint-lg} {
        font-size: 24px;
      }
      @media #{$breakpoint-md} {
        font-size: 20px;
      }
    }
    .product-price {
      margin-bottom: 0;
      font-family: $font-ubuntu;
      font-size: 24px;
      color: $hover-color;
      font-weight: 700;
      @media #{$breakpoint-md} {
        font-size: 20px;
      }
      span {
        display: inline-block;
        font-family: $font-ubuntu;
        font-size: 24px;
        color: $text-color;
        font-weight: 700;
        text-decoration: line-through;
        margin-right: 15px;
      }
    }
    .product-desc {
      font-size: 16px;
      @media #{$breakpoint-md} {
        font-size: 14px;
      }
    }
    .nice-select {
      position: relative;
      z-index: 100;
      border: 1px solid $border-color;
      border-radius: 0;
      font-size: 14px;
      font-weight: 500;
      height: 60px;
      line-height: 60;
      margin: 0;
      text-transform: uppercase;
      width: 220px;
      .current {
        line-height: 60px;
      }
      .list {
        background-color: #f6f6f6;
        border-radius: 0;
        border: none;
        width: 100%;
        .option.selected {
          color: $bg-default;
        }
      }
    }
    .cart-fav-box {
      .favme {
        cursor: pointer;
        color: #ff5050;
        font-size: 24px;
        &:hover {
          color: #ce4444 !important;
        }
      }
    }
  }

  .full-description-container {
    width: 100%;
    padding: 5% 0;
  }
}
