@import './theme_color';
@import './variables';
@import './mixin';
@import './responsive';

/* [Master Stylesheet v-1.0.0] */

/* :: 1.0 Import Web Fonts */

@import url('https://fonts.googleapis.com/css?family=Poppins:300,400,500,600,700|Ubuntu:300,400,500,700');

/* :: 2.0 Import All CSS */

@import '../css/bootstrap.min.css';
@import '../css/classy-nav.min.css';
@import '../css/owl.carousel.css';
@import '../css/animate.css';
@import '../css/magnific-popup.css';
// @import '../css/jquery-ui.min.css';
@import '../css/nice-select.css';
@import '../css/font-awesome.min.css';
@import '../css/core-style.css';
@import '../css/bootstrap.min.css';

/* :: 3.0 Common/Base CSS */

* {
    margin: 0;
    padding: 0;
}

body {
    font-family: $font-poppins;
    font-size: 14px;
    background-color: $white-color;
    margin-top: 85px;
    @media #{$breakpoint-xs} {
        margin-top: 80px;
    }
}

h1,
h2,
h3,
h4,
h5,
h6 {
    color: $heading-color;
    line-height: 1.3;
    font-weight: 700;
    font-family: $font-ubuntu;
}

p {
    color: $text-color;
    font-size: 14px;
    line-height: 2;
    font-weight: 400;
}

// SCROLLBAR
/* width */
::-webkit-scrollbar {
    width: 6px;
}

/* Track */
::-webkit-scrollbar-track {
    background: #f1f1f1; 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
    background: rgba(136, 136, 136, 0.582); 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: rgba(85, 85, 85, 0.582); 
}

a {
    @include transition(all 500ms ease 0s);
    text-decoration: none;
    outline: 0 solid transparent;
    color: $dark-color;
    font-weight: 600;
    font-size: 12px;
    &:hover,
    &:focus {
        @include transition(all 500ms ease 0s);
        text-decoration: none;
        outline: 0 solid transparent;
        color: $dark-color;
        font-weight: 600;
        font-size: 12px;
    }
}

ul,
ol {
    margin: 0;
    li {
        list-style: none;
    }
}

img {
    height: auto;
    max-width: 100%;
}

.page-item.active .page-link {
    background-color: $text-color !important;
    color: white !important;
}

.container-fluid {
    padding: 5px 0;
}

/* Spacing */

.mt-15 {
    margin-top: 15px !important;
}

.mt-30 {
    margin-top: 30px !important;
}

.mt-50 {
    margin-top: 50px !important;
}

.mt-70 {
    margin-top: 70px !important;
}

.mt-100 {
    margin-top: 100px !important;
}

.mb-15 {
    margin-bottom: 15px !important;
}

.mb-30 {
    margin-bottom: 30px !important;
}

.mb-50 {
    margin-bottom: 50px !important;
}

.mb-70 {
    margin-bottom: 70px !important;
}

.mb-100 {
    margin-bottom: 100px !important;
}

.ml-15 {
    margin-left: 15px !important;
}

.ml-30 {
    margin-left: 30px !important;
}

.ml-50 {
    margin-left: 50px !important;
}

.mr-15 {
    margin-right: 15px !important;
}

.mr-30 {
    margin-right: 30px !important;
}

.mr-50 {
    margin-right: 50px !important;
}

/* Height */

.height-400 {
    height: 400px !important;
}

.height-500 {
    height: 500px !important;
}

.height-600 {
    height: 600px !important;
}

.height-700 {
    height: 700px !important;
}

.height-800 {
    height: 800px !important;
}

/* Section Padding */

.section-padding-80 {
    padding-top: 80px;
    padding-bottom: 80px;
}

.section-padding-80-0 {
    padding-top: 80px;
    padding-bottom: 0;
}

.section-padding-0-80 {
    padding-top: 0;
    padding-bottom: 80px;
}

/* Section Heading */

.section-heading {
    margin-bottom: 50px;
    position: relative;
    z-index: 1;
    h2 {
        font-size: 30px;
        margin-bottom: 0;
        text-transform: capitalize;
        font-weight: 600;
    }
    &.text-left {
        text-align: left !important;
    }
}

/* Miscellaneous */

.bg-img {
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
}

.bg-white {
    background-color: #ffffff !important;
}

.bg-dark {
    background-color: #000000 !important;
}

.bg-transparent {
    background-color: transparent !important;
}

.font-bold {
    font-weight: 700;
}

.font-light {
    font-weight: 300;
}

.bg-overlay,
.bg-overlay-white {
    position: relative;
    z-index: 2;
    background-position: center center;
    background-size: cover;
    &:hover {
        position: absolute;
        z-index: -1;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        content: "";
    }
}

.bg-overlay {
    &:hover {
        background-color: rgba(0, 0, 0, 0.5);
    }
}

.bg-overlay-white {
    &:hover {
        background-color: rgba(255, 255, 255, 0.9);
    }
}

/* ScrollUp */

#scrollUp {
    z-index: 7 !important;
    background-color: #ff084e;
    border-radius: 0;
    bottom: 60px;
    box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.3);
    color: $white-color;
    font-size: 24px;
    height: 40px;
    line-height: 40px;
    right: 60px;
    text-align: center;
    width: 40px;
    @media #{$breakpoint-xs} {
        bottom: 30px;
        right: 30px;
    }
}

body.swal2-toast-shown .swal2-container {
    top: 90px !important;
    z-index: 99999;
}

/* :: 5.0 Welcome Area CSS */

.welcome_area {
    position: relative;
    z-index: 1;
    width: 100%;
    height: 600px;
    @media #{$breakpoint-md} {
        height: 450px;
    }
    @media #{$breakpoint-xs} {
        height: 350px;
    }
    .hero-content {
        h6 {
            font-size: 18px;
            color: $text-color;
            margin-bottom: 10px;
        }
        h2 {
            font-size: 60px;
            color: $heading-color;
            margin-bottom: 50px;
            @media #{$breakpoint-md} {
                font-size: 48px;
            }
            @media #{$breakpoint-xs} {
                font-size: 30px;
            }
        }
    }
}

/* :: 6.0 Top Catagory Area CSS */

.single_catagory_area {
    position: relative;
    z-index: 1;
    width: 100%;
    height: 240px;
    @include trans-duration(500ms);
    @media #{$breakpoint-md} {
        height: 180px;
    }
    @media #{$breakpoint-xs} {
        height: 180px;
    }
    @media #{$breakpoint-sm} {
        margin-bottom: 30px;
    }
    &:after {
        @include trans-duration(500ms);
        content: "";
        width: 100%;
        height: 100%;
        background-color: rgba(255, 255, 255, 0.6);
        position: absolute;
        top: 0;
        left: 0;
        z-index: -10;
    }
    .catagory-content {
        a {
            display: block;
            font-size: 30px;
            font-weight: 700;
            color: $dark-color;
            letter-spacing: 1px;
            text-transform: uppercase;
            @media #{$breakpoint-md} {
                font-size: 20px;
            }
            @media #{$breakpoint-xs} {
                font-size: 18px;
            }
        }
    }
    &:hover {
        .catagory-content {
            a {
                color: $bg-default;
            }
        }
    }
}

/* :: 7.0 CTA Area CSS */

.cta-area {
    position: relative;
    z-index: 1;
    .cta-content {
        width: 100%;
        height: 550px;
        @media #{$breakpoint-xs} {
            height: 400px;
        }
        h6 {
            font-size: 24px;
            color: $hover-color;
            margin-bottom: 10px;
        }
        h2 {
            font-size: 60px;
            color: $heading-color;
            margin-bottom: 50px;
            @media #{$breakpoint-md} {
                font-size: 48px;
            }
            @media #{$breakpoint-xs} {
                font-size: 30px;
            }
        }
        .cta--text {
            padding-right: 150px;
            @media #{$breakpoint-md} {
                padding-right: 50px;
            }
            @media #{$breakpoint-xs} {
                padding-right: 0;
            }
        }
    }
}

/* :: 9.0 Brands Area CSS */

.brands-area {
    position: relative;
    z-index: 1;
    background-color: $bg-gray;
    padding: 100px 5%;
    @media #{$breakpoint-xs} {
        @include flex-wrap(wrap);
        padding: 100px 5% 70px;
    }
    .single-brands-logo {
        @media #{$breakpoint-xs} {
            @include flex(0 0 33.33333%);
            width: 33.33333%;
            max-width: 33.33333%;
        }
        img {
            max-width: 120px;
            @media #{$breakpoint-md} {
                max-width: 90px;
            }
            @media #{$breakpoint-xs} {
                max-width: 80px;
                margin-bottom: 30px;
            }
        }
    }
}


/* :: 12.0 Breadcumb Area CSS */

.breadcumb_area {
    position: relative;
    z-index: 1;
    width: 100%;
    height: 140px;
    &:after {
        background-color: rgba(255, 255, 255, 0.9);
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        z-index: -5;
        content: '';
    }
    .page-title {
        h2 {
            font-size: 30px;
            text-transform: uppercase;
            font-weight: 700;
            font-family: $font-ubuntu;
            letter-spacing: 1px;
            margin-bottom: 0;
        }
    }
    &.breadcumb-style-two {
        width: 100%;
        height: 390px;
        @media #{$breakpoint-md} {
            height: 300px;
        }
        @media #{$breakpoint-xs} {
            height: 250px;
        }
        .page-title {
            h2 {
                font-size: 72px;
                text-transform: uppercase;
                font-weight: 700;
                font-family: $font-ubuntu;
                letter-spacing: 1px;
                margin-bottom: 0;
                color: $bg-default;
                @media #{$breakpoint-md} {
                    font-size: 48px;
                }
                @media #{$breakpoint-xs} {
                    font-size: 30px;
                }
            }
        }
    }
}

.product-topbar {
    position: relative;
    z-index: 100;
    margin-bottom: 40px;
    .total-products {
        p {
            margin-bottom: 0;
            font-size: 12px;
            font-weight: 600;
            color: $heading-color;
            text-transform: uppercase;
            letter-spacing: 0.75px;
            span {
                color: $bg-default;
            }
        }
    }
    .product-sorting {
        p {
            margin-bottom: 0;
            font-size: 12px;
            font-weight: 600;
            color: $heading-color;
            text-transform: uppercase;
            letter-spacing: 0.75px;
            margin-right: 15px;
        }
        form {
            select {
                background-color: transparent;
                border: none;
                option {
                    font-size: 12px;
                    font-weight: 600;
                    color: $heading-color;
                    text-transform: uppercase;
                    letter-spacing: 0.75px;
                }
            }
        }
    }
    .nice-select {
        border: none;
        font-size: 14px;
        font-weight: 500;
        height: auto;
        line-height: 1.5;
        padding-left: 0;
        margin: 0;
        text-transform: uppercase;
        .list {
            background-color: #f6f6f6;
            border-radius: 0;
            border: none;
            .option.selected {
                color: $bg-default;
            }
        }
    }
}

/* :: 15.0 Blog Area CSS */

.blog-wrapper {
    position: relative;
    z-index: 1;
    .single-blog-area {
        position: relative;
        z-index: 1;
        box-shadow: 0 0 4px rgba(0, 0, 0, 0.15);
        @include trans-duration(500ms);
        overflow: hidden;
        img {
            width: 100%;
            @include trans-duration(500ms);
        }
        .post-title {
            @include trans-duration(500ms);
            background-color: $white-color;
            padding: 20px 40px;
            position: absolute;
            bottom: 0;
            left: 0;
            width: 85%;
            height: auto;
            z-index: 10;
            @media #{$breakpoint-xs} {
                padding: 20px;
            }
            a {
                display: block;
                font-size: 18px;
                font-weight: 600;
                line-height: 1.5;
                @media #{$breakpoint-xs} {
                    font-size: 14px;
                }
                @media #{$breakpoint-sm} {
                    font-size: 18px;
                }
            }
        }
        .hover-content {
            @include trans-duration(500ms);
            background-color: $white-color;
            background-color: $white-color;
            padding: 20px 40px;
            position: absolute;
            width: 85%;
            height: 100%;
            z-index: 100;
            top: 0;
            left: 0;
            opacity: 0;
            visibility: hidden;
            @media #{$breakpoint-lg} {
                padding: 20px;
            }
            @media #{$breakpoint-xs} {
                padding: 20px;
            }
            .hover-post-title {
                a {
                    display: block;
                    font-size: 18px;
                    font-weight: 600;
                    line-height: 1.5;
                    margin-bottom: 20px;
                    @media #{$breakpoint-lg} {
                        font-size: 16px;
                        margin-bottom: 10px;
                    }
                    @media #{$breakpoint-xs} {
                        font-size: 14px;
                        margin-bottom: 10px;
                    }
                    @media #{$breakpoint-sm} {
                        font-size: 18px;
                        margin-bottom: 20px;
                    }
                    &:hover {
                        color: $bg-default;
                    }
                }
            }
            p {
                @media #{$breakpoint-xs} {
                    display: none;
                }
                @media #{$breakpoint-xs-landscape} {
                    display: block;
                    font-size: 14px;
                    line-height: 1.7;
                }
            }
            > a {
                display: block;
                font-size: 12px;
                font-weight: 600;
                margin-bottom: 0;
                color: $bg-default;
                letter-spacing: 1.5px;
                text-transform: uppercase;
                margin-top: 50px;
                @media #{$breakpoint-lg} {
                    margin-top: 15px;
                }
                @media #{$breakpoint-xs} {
                    margin-top: 30px;
                }
            }
        }
        &:hover,
        &:focus {
            .hover-content {
                opacity: 1;
                visibility: visible;
            }
            img {
                @include transform(scale(1.1));
            }
        }
    }
}

.single-blog-wrapper {
    position: relative;
    z-index: 1;
    .single-blog-content-wrapper {
        position: relative;
        z-index: 1;
        @include flex-wrap(wrap);
        .single-blog--text {
            @include flex(0 0 75%);
            max-width: 75%;
            width: 75%;
            padding: 70px 7%;
            @media #{$breakpoint-lg} {
                @include flex(0 0 65%);
                max-width: 65%;
                width: 65%;
            }
            @media #{$breakpoint-md} {
                @include flex(0 0 60%);
                max-width: 60%;
                width: 60%;
            }
            @media #{$breakpoint-xs} {
                @include flex(0 0 100%);
                max-width: 100%;
                width: 100%;
            }
            h2 {
                font-size: 30px;
                line-height: 1.5;
                margin-bottom: 50px;
                @media #{$breakpoint-lg} {
                    font-size: 24px;
                    margin-bottom: 30px;
                }
                @media #{$breakpoint-md} {
                    font-size: 20px;
                    margin-bottom: 20px;
                }
                @media #{$breakpoint-xs} {
                    font-size: 18px;
                    margin-bottom: 20px;
                }
                @media #{$breakpoint-sm} {
                    font-size: 24px;
                }
            }
            p {
                font-size: 18px;
                font-weight: 300;
                margin-bottom: 50px;
                @media #{$breakpoint-md} {
                    font-size: 16px;
                }
                @media #{$breakpoint-xs} {
                    font-size: 14px;
                }
            }
            blockquote {
                margin-bottom: 50px;
                h6 {
                    font-size: 18px;
                    line-height: 2;
                    @include display-flex(flex);
                    i {
                        margin-right: 30px;
                        font-size: 30px;
                        color: $bg-default;
                    }
                }
                span {
                    margin-left: 60px;
                    font-size: 16px;
                    font-weight: 600;
                    color: rgba(0, 0, 0, 0.6);
                }
            }
        }
        .related-blog-post {
            @include flex(0 0 25%);
            max-width: 25%;
            width: 25%;
            @media #{$breakpoint-lg} {
                @include flex(0 0 35%);
                max-width: 35%;
                width: 35%;
            }
            @media #{$breakpoint-md} {
                @include flex(0 0 40%);
                max-width: 40%;
                width: 40%;
            }
            @media #{$breakpoint-xs} {
                @include flex(0 0 100%);
                max-width: 100%;
                width: 100%;
            }
            .single-related-blog-post {
                position: relative;
                z-index: 1;
                overflow: hidden;
                @include trans-duration(500ms);
                img {
                    width: 100%;
                    @include trans-duration(500ms);
                }
                a {
                    display: block;
                    position: absolute;
                    width: 100%;
                    height: 100%;
                    top: 0;
                    left: 0;
                    background-color: rgba(0, 0, 0, 0.6);
                    @include display-flex(flex);
                    @include align-items(center);
                    @include justify-content-between;
                    padding: 5px 50px;
                    h5 {
                        color: $white-color;
                        text-align: center;
                        font-size: 18px;
                        margin-bottom: 0;
                        line-height: 1.5;
                    }
                }
                &:hover,
                &:focus {
                    img {
                        @include transform(scale(1.2));
                    }
                }
            }
        }
    }
}

/* :: 16.0 Regular Page Area CSS */

.regular-page-content-wrapper {
    position: relative;
    z-index: 1;
    .regular-page-text {
        h2 {
            font-size: 30px;
            line-height: 1.5;
            margin-bottom: 50px;
        }
        p {
            font-size: 18px;
            font-weight: 300;
            margin-bottom: 50px;
        }
        blockquote {
            margin-bottom: 50px;
            h6 {
                font-size: 18px;
                line-height: 2;
                @include display-flex(flex);
                i {
                    margin-right: 30px;
                    font-size: 30px;
                    color: $bg-default;
                }
            }
            span {
                margin-left: 60px;
                font-size: 16px;
                font-weight: 600;
                color: rgba(0, 0, 0, 0.6);
            }
        }
    }
}

/* :: 17.0 Contact Area CSS */

.contact-area {
    position: relative;
    z-index: 1;
    @include flex-wrap(wrap);
    .google-map {
        @include flex(0 0 60%);
        max-width: 60%;
        width: 60%;
        @media #{$breakpoint-md} {
            @include flex(0 0 55%);
            max-width: 55%;
            width: 55%;
        }
        @media #{$breakpoint-xs} {
            @include flex(0 0 100%);
            max-width: 100%;
            width: 100%;
        }
        #googleMap {
            width: 100%;
            height: 685px;
            @media #{$breakpoint-xs} {
                height: 400px;
            }
        }
    }
    .contact-info {
        @include flex(0 0 40%);
        max-width: 40%;
        width: 40%;
        padding: 50px 5%;
        @media #{$breakpoint-md} {
            @include flex(0 0 45%);
            max-width: 45%;
            width: 45%;
            padding: 50px 3%;
        }
        @media #{$breakpoint-xs} {
            @include flex(0 0 100%);
            max-width: 100%;
            width: 100%;
            padding: 50px 5%;
        }
        h2 {
            font-size: 30px;
            margin-bottom: 20px;
        }
        p {
            font-size: 16px;
            font-weight: 300;
        }
        .contact-address {
            p {
                font-size: 12px;
                font-weight: 600;
                margin-bottom: 10px;
                span {
                    font-weight: 600;
                    color: $dark-color;
                    width: 100px;
                    display: inline-block;
                    text-transform: uppercase;
                }
                &:last-child {
                    margin-top: 100px;
                    a {
                        font-size: 14px;
                        font-weight: 300;
                        &:hover {
                            color: $bg-default;
                        }
                    }
                }
            }
        }
    }
}
/* ##### The End ##### */