@import 'src/assets/scss/theme_color';
@import 'src/assets/scss/variables';
@import 'src/assets/scss/mixin';
@import 'src/assets/scss/responsive';

/* Cart Area CSS */


.right-side-cart-area .cart-content .empty-cart{
    width: 100%;
    display: flex;
    justify-content: center;
    align-content: center;
    flex-wrap: wrap;
    img {
        user-select: none;
    }
}

.right-side-cart-area .cart-content .cart-list {
    overflow-y: scroll !important;
}

.right-side-cart-area .cart-content .cart-list .single-cart-item .product-image .cart-item-desc h6 {
    margin-bottom: 10px !important;
}


.cart-item-name {
    font-weight: 400 !important;
    text-transform: none !important;
    margin-bottom: 8px !important;
}

.summary-header {
    margin-top: 20px;
}

.product-remove {
    z-index: 9;

    i {
        font-size: 1.2rem;
    }

    i:hover {
        font-size: 1.4rem;
        transition: cubic-bezier();
        transition-duration: .2s;
       cursor: pointer;
    }

    position: absolute;
    top: 15px;
    right: 15px;
    color: $white-color;
    font-size: 12px;
    padding: 5px;
    @media #{$breakpoint-xs} {
        top: 5px;
        right: 5px;
    }
}

.right-side-cart-area {
    position: fixed;
    width: 670px;
    height: 100%;
    top: 0;
    right: -800px;
    background-color: $white-color;
    z-index: 1100;
    box-shadow: 0 0 30px 0 rgba(0, 0, 0, 0.1);
    @include trans-duration(800ms);
    @media #{$breakpoint-md} {
        width: 500px;
        right: -600px;
    }
    @media #{$breakpoint-xs} {
        width: 270px;
        right: -400px;
    }
    @media #{$breakpoint-sm} {
        width: 400px;
        right: -550px;
    }
    &.cart-on {
        right: 0;
    }
    .cart-button {
        :hover {
            cursor: pointer;
        }
        
        #rightSideCart {
            position: absolute;
            top: 0;
            right: 100%;
            z-index: 100;
            width: 90px;
            display: block;
            text-align: center;
            border-left: 1px solid $border-color;
            height: 85px;
            line-height: 80px;
            background-color: $bg-gray;
            @media #{$breakpoint-xs} {
                width: 50px;
                height: 50px;
                line-height: 50px;
            }
            img {
                max-width: 20px;
            }
            span {
                font-family: $font-ubuntu;
                font-size: 18px;
                color: $bg-default;
                font-weight: 700;
                position: absolute;
                top: -10px;
            }
        }
    }
    .cart-content {
        position: relative;
        z-index: 1;
        width: 100%;
        overflow-x: hidden;
        overflow-y: scroll;
        height: 100%;

        .cart-list {
            position: relative;
            z-index: 10;
            @include flex(0 0 190px);
            width: 190px;
            @media #{$breakpoint-xs} {
                @include flex(0 0 100px);
                width: 100px;
            }
            @media #{$breakpoint-sm} {
                @include flex(0 0 150px);
                width: 150px;
            }
            .single-cart-item {
                position: relative;
                z-index: 1;
                @include trans-duration(500ms);
                .product-image {
                    position: relative;
                    z-index: 1;
                    display: block;
                    .cart-item-desc {
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: 100%;
                        height: 100%;
                        background-color: rgba(0, 0, 0, 0.6);
                        padding: 50px 15px 15px;
                        @include trans-duration(500ms);
                        @media #{$breakpoint-xs} {
                            padding: 5px;
                        }
                        @media #{$breakpoint-sm} {
                            padding: 30px 20px;
                        }
                        
                        .badge {
                            display: block;
                            font-weight: 600;
                            font-size: 10px;
                            color: rgba(255, 255, 255, 0.5);
                            text-transform: uppercase;
                            margin-bottom: 5px;
                            text-align: left;
                            padding: 0;
                        }

                        .item-quantity {
                            font-size: 22px;
                            margin: 0;
                        }

                        h6 {
                            font-size: 12px;
                            color: $white-color;
                            text-transform: capitalize;
                            margin-bottom: 30px;
                            @media #{$breakpoint-xs} {
                                margin-bottom: 10px;
                            }
                        }
                        .size,
                        .color {
                            display: block;
                            font-weight: 600;
                            font-size: 10px;
                            color: rgba(255, 255, 255, 0.5);
                            text-transform: uppercase;
                            margin-bottom: 5px;
                            line-height: 1;
                        }
                        .price {
                            font-family: $font-ubuntu;
                            font-size: 16px;
                            font-weight: 700;
                            margin-bottom: 0;
                            margin-top: 30px;
                            color: $white-color;
                            @media #{$breakpoint-xs} {
                                margin-top: 10px;
                                font-size: 12px;
                            }
                        }
                    }
                }
                &:hover {
                    .product-image .cart-item-desc {
                        background-color: rgba(0, 0, 0, 0.8);
                    }
                }
            }
        }
        .cart-amount-summary {
            overflow-y: scroll;
            padding: 100px 10%;
            position: relative;
            z-index: 10;
            @include flex(0 0 calc(100% - 190px));
            width: calc(100% - 190px);
            @media #{$breakpoint-xs} {
                @include flex(0 0 calc(100% - 100px));
                width: calc(100% - 100px);
                padding: 50px 15px;
            }
            @media #{$breakpoint-sm} {
                @include flex(0 0 calc(100% - 150px));
                width: calc(100% - 150px);
                padding: 50px 30px;
            }
            h2 {
                font-size: 30px;
                margin-bottom: 100px;
                @media #{$breakpoint-xs} {
                    font-size: 24px;
                    margin-bottom: 30px;
                }
                @media #{$breakpoint-xs} {
                    font-size: 30px;
                    margin-bottom: 50px;
                }
            }
            .summary-table {
                li {
                    margin-bottom: 20px;
                    color: $dark-color;
                    font-size: 14px;
                    letter-spacing: 0.75px;
                    text-transform: uppercase;
                    font-weight: 600;
                    @include display-flex(flex);
                    @include justify-content-between;
                    @media #{$breakpoint-xs} {
                        font-size: 12px;
                        margin-bottom: 10px;
                    }
                    @media #{$breakpoint-sm} {
                        font-size: 14px;
                        margin-bottom: 15px;
                    }
                    span {
                        &:last-child {
                            font-weight: 700;
                            font-family: $font-ubuntu;
                        }
                    }
                }
            }
            .essence-btn {
                @media #{$breakpoint-xs} {
                    min-width: 140px;
                    width: 140px;
                    padding: 0 15px;
                }
            }
        }
    }
}

/* Essence Button */

.essence-btn {
    display: inline-block;
    min-width: 170px;
    height: 50px;
    color: $white-color;
    border: none;
    border-radius: 0;
    padding: 0 40px;
    text-transform: uppercase;
    font-size: 12px;
    line-height: 50px;
    background-color: $bg-default;
    letter-spacing: 1.5px;
    font-weight: 600;
    &:hover,
    &:focus {
        color: $white-color;
        background-color: $hover-color;
    }
}

.cart-bg-overlay {
    &.cart-bg-overlay-on {
        @include trans-duration(1000ms);
        position: fixed;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        background-color: rgba(0, 0, 0, 0.4);
        z-index: 1050;
    }
}
